import { Component, OnInit } from '@angular/core';
import {ValidateService} from '../../services/validate.service';
import {AuthService} from '../../services/auth.service';
// import {FlashMessagesService} from 'angular2-flash-messages';
import {Router} from '@angular/router';

@Component({
  selector: 'app-register',
  templateUrl: './register.component.html',
  styleUrls: ['./register.component.css']
})
export class RegisterComponent implements OnInit {
name: String;
username: String;
email: String;
password: String;

//have to use this syntax in constructor head to inject functions
  constructor(
  private validateService: ValidateService,
  private authService: AuthService,
  // private flashMessage: FlashMessagesService,
  private router: Router) { }

  ngOnInit() {

  }

//Angular Equivelent of Middleware
onRegisterSubmit(){
  const user = {
    name: this.name,
    username: this.username,
    email: this.email,
    password: this.password
  }
//Required Fields
if(!this.validateService.validateRegister(user)){
// this.flashMessage.show("please fill in all required fields", {cssClass: 'alert-danger', timeout: 3000});
return false;
}

//Validte Email
if(!this.validateService.validateEmail(user.email)){
// this.flashMessage.show("please use a valid email", {cssClass: 'alert-warning', timeout: 3000});
return false;
}

//Register user
this.authService.registerUser(user).subscribe(data => {
if(data.success){
  // this.flashMessage.show("You are now registered!", {cssClass: 'alert-success', timeout: 3000});
  this.router.navigate(['/login']);
}else{
    // this.flashMessage.show("Oops something went wrong", {cssClass: 'alert-danger', timeout: 3000});
      this.router.navigate(['/register']);
}
});

}
}



